/**
 * @file parseParams.js
 * @description parses and generates param code
 * 
 * @author Arth Gajjar <iarthstar@gmail.com>
 */



// module imports
let UI = require('sketch/ui');

const setAlpha = (elem) => {
    if (elem.style.opacity != 1){
        return `\n  , alpha ${elem.style.opacity.toPrecision(2)}`;   
    } else 
        return "";
};


const setBackground = (elem) => {
    if (elem.style.fills.length) {
        let fill = elem.style.fills.filter(fill => fill.enabled).pop();
        switch(fill.fill){
            case 'Color': 
                return `\n  , background "#${fill.color.slice(7,9).toUpperCase() + fill.color.slice(1,7).toUpperCase()}"`;  
        }
    } else 
        return "";
};


const setBorder = (elem) => {
    if (elem.style.borders.length) {
        let border = elem.style.borders.filter(border => border.enabled).pop();
        switch(border.fillType){
            case 'Color': 
                return `\n  , stroke "${border.thickness},#${border.color.slice(7,9).toUpperCase() + border.color.slice(1,7).toUpperCase()}"`;  
        }
    } else
        return "";
};


const setCornerRadius = (elem) => {
    let points = elem.points.map(point => point.cornerRadius);
    if(Math.max(...points))
        return `\n  , cornerRadius ${parseInt(Math.max(...points))}`;
    else 
        return "";
};


const setFont = (elem) => {
    let font = elem.style.fontFamily;
    let weight = "";
    if (font == "Roboto") {
        switch(elem.style.fontWeight){
            case 3:     weight = "Light";   break;
            case 5:     weight = "Regular"; break;
            case 6:     weight = "Medium";  break;
            case 9:     weight = "Bold";    break;
            case 11:    weight = "Black";   break;
            default :   weight = "Regular";
        }
    } else if (font == "SF Pro Display") {
        switch(elem.style.fontWeight){
            case 2:     weight = "UltraLight";  break;
            case 3:     weight = "Light";       break;
            case 5:     weight = "Regular";     break;
            case 6:     weight = "Medium";      break;
            case 8:     weight = "SemiBold";    break;
            case 9:     weight = "Bold";        break;
            case 10:    weight = "Heavy";       break;
            case 11:    weight = "Black";       break;
            default:    weight = "Regular";
        }
    } else if (font == "Gilroy") {
        switch(elem.style.fontWeight){
            case 2:     weight = "Thin";        break;
            case 3:     weight = "Light";       break;
            case 5:     weight = "Regular";     break;
            case 6:     weight = "Medium";      break;
            case 8:     weight = "SemiBold";    break;
            case 9:     weight = "Bold";        break;
            case 10:    weight = "ExtraBold";   break;
            default:    weight = "Regular";
        }
    } else {
        switch(elem.style.fontWeight){
            case 2:     
            case 3:     weight = "Light";       break;
            case 5:     weight = "Regular";     break;
            case 6:     weight = "Medium";      break;
            case 8:     weight = "SemiBold";    break;
            case 9:     weight = "Bold";        break;
            case 10:    weight = "Heavy";       break;
            case 11:    weight = "Black";       break;
            default:    weight = "Regular";
        }
    }
    return `\n  , fontStyle "${font.split(" ").join("")}-${weight}"`;
};


const setFrame = (elem) => {
    return `\n  [ height $ V ${parseInt(elem.frame.height)} \n  , width $ V ${parseInt(elem.frame.width)}`;
};


const setGravity = (elem) => {
    let gravity = '';
    switch(elem.style.alignment){
        case 'left':    return "";
        case 'right':   gravity = 'RIGHT';  break;
        case 'center':  gravity = 'CENTER'; break;
        default: gravity = 'LEFT';
    }
    return `\n  , gravity ${gravity}`;
};


const setImageUrl = (elem) => {
    
    UI.getInputFromUser(
        "Set ImageUrl", {
            initialValue: elem.name,
        },
        (err, value) => {
            if (err) {
                return;
            }
            elem.name = value;

            var sketch = require('sketch/dom');
            let options = { scales: '3', output: "~/Desktop/", overwriting: true};
            sketch.export(elem, options);
        }
    );
    return `\n  , imageUrl "${elem.name}"`;    
};


const setKerning = (elem) => {
    if (elem.style.kerning != null){
        let kerning = elem.style.kerning.toPrecision(2) > 0 ? elem.style.kerning.toPrecision(2) : `(${elem.style.kerning.toPrecision(2)})`
        return `\n  , letterSpacing ${kerning}`;   
    } else 
        return "";
};


const setText = (elem) => {
    let text = '';
    switch(elem.style.textTransform){
        case 'none': text = elem.text; break;
        case 'uppercase': text = elem.text.toUpperCase(); break;
        case 'lowercase': text = elem.text.toLowerCase(); break;
        default: text = elem.text;
    }
    return `\n  , text "${text.replace('\n',`\\n`)}"`;
};

/**
 * @module params
 */
module.exports = {
    setAlpha,
    setBackground,
    setBorder,
    setCornerRadius,
    setFont,
    setFrame,
    setGravity,
    setImageUrl,
    setKerning,
    setText
};