// module imports
import sketch from 'sketch';
import * as params from './parseParams.js';

let UI = require('sketch/ui');
let paste = NSPasteboard.generalPasteboard();


//
// ─────────────────────────────────────────────────────── INIT PLUGIN ─────
//

/**
 * Press 'CMD SHIFT P', to invoke the function.
 * Gets the selected Layers and iterates on the layers to generate Presto-Dom string
 */
export default function () {
    let selection = sketch.getSelectedDocument().selectedLayers;

    let elem = selection.isEmpty ? [] : selection.layers;
    let nodes = "";
    elem.forEach(e => {
        nodes += genNode(e);
    });

    if (elem.length) {
        paste.clearContents();
        paste.setString_forType(nodes,"public.utf8-plain-text");
        UI.message("Presto-Dom node generated and is ready for pasting...");
    } else {
        UI.alert('No Selection', "Please Select a layer and try again...");
    }
}


/**
 * Takes a Sketch Layer, parses on params and returns the generated node string.
 * @param {object} elem sketch layer
 * @returns {string} generated Presto-Dom node string
 */
const genNode = (elem) => {
    let node = '';
    node += params.setFrame(elem);
    node += params.setAlpha(elem);
    switch (elem.type) {
        case 'Text':
            elem.fixedWidth = false;
            node = 'textView' + node;
            node += params.setText(elem);
            node += params.setFont(elem);
            node += `\n  , textSize ${elem.style.fontSize}`;
            node += `\n  , color "#${elem.style.textColor.slice(1,7).toUpperCase()}"`;
            node += params.setKerning(elem);
            node += `\n  ]\n`;
            break;
        case 'Group':
            node = 'linearLayout' + node;
            node += `\n  ][]\n`;
            break;
        case 'Image':
            node = 'imageView' + node;
            node += params.setImageUrl(elem);
            node += `\n  ]\n`;
            break;
        case 'ShapePath':
            switch(elem.shapeType){
                case 'Rectangle': 
                    node = 'linearLayout' + node;
                    node += params.setBackground(elem);
                    node += params.setBorder(elem);
                    node += params.setCornerRadius(elem);
                    // To do : setShadow(elem)
                    node += `\n  ][]\n`;
                break;
                // To do : Handle more shapes 
            }
            break;
        default:
            node = 'linearLayout' + node;
            node += `\n  ][]\n`;
    }
    return node;
}